
.gv-legend-title {
  position: relative;
  display: block;
  padding: 0.3rem 0.5rem;
  margin-bottom: -1px;
  color: #ccc;
  cursor: default;
  font-weight: bold;
  
font-family: 'Raleway', Arial, sans-serif !important;
font-size: 14px;
}
.gv-legend-title span {
  font-size: 14px;
  font-weight: bold;
}
.gv-legend-close {
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  float: right;
  font-size: 24px;
  line-height: 1;
  color: #ffffff;
  margin-right: -8px;
  margin-top: -2px;
  opacity: 0.5;
}
.gv-legend-buttons {
  cursor: pointer;
  border: 0;
  -webkit-appearance: none;
  float: right;
  line-height: 1;
  font-size: 12px !important;
  padding-top: 3px !important;
  padding-left: 0px !important;
}

/* .gv-legend-buttons span {
  font-family: 'Raleway', Arial, sans-serif !important;
  font-size: 14px;
  font-weight: bold;
  vertical-align: top;
} */
.gv-list-group {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  background-color: #fff;
  width: 260px;
}
.gv-list-legend-layer-item {
  position: relative;
  display: block;
  /* padding: 0.1rem 0.5rem; */
  padding-left: 0.3rem;
  margin-bottom: -2px;
  margin-top: -2px;
  background-color: #fff;
  border: 1px solid #ddd;
}
.gv-list-legend-layer-disabled-item {
  opacity: 0.3;
  filter: alpha(opacity=30);
}
.gv-legend-layer-icon {
  /* padding: 2px 2px 2px 2px; */
  padding-top: 3px;
}
.gv-legend-layer-icon img {
  width: 24px;
  height: 24px;
}
.gv-legend-map-tools {
  position: relative;
  padding-top: 2px;
  padding-bottom: 2px;
  /* ALlineato a destra
        padding: 0rem 13.5rem;
*/
  width: 260px;
}
.gv-list-legend-map-item {
  position: relative;
  display: block;
  padding: 0.1rem 0.5rem;
  margin-bottom: -1px;
  border: 1px solid #ddd;
  font-size: 13px;
  font-weight: bold;
}
.gv-legend-map-tools-add {
  position: relative;
  right: 0;
  bottom: 0;
  margin-bottom: 5px;
  font-size: 14px;
}
.gv-legend-map-tools span {
  font-family: 'Raleway', Arial, sans-serif !important;
  font-size: 12px;
  font-weight: bold;
}
.gv-layer-visibility-span {
  position: absolute;
  top: 16%;
  left: 29px;
}
.gv-layer-title-span {
  position: absolute;
  top: 50%;
  left: 55px;
  margin-top: -7px;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  max-width: 180px;
}
.gv-legend-footer {
  display: block;
  padding: 0.3rem 0.5rem;
  margin-bottom: -1px;
  cursor: default;
}
#gv-legend {
  position: relative;
  float: right;
  right: 0;
  top: 0;
  margin-right: 10px;
  margin-top: 10px;
  width: 260px;
  z-index: 800;
  max-height: 430px;
}
#gv-legend-title {
  height: 30px;
}
#gv-legend-body {
  width: 260px;
  max-height: 400px;
  cursor: default;
  overflow: hidden;
}
#gv-legend-body:hover {
  overflow-y: scroll;
}
@media (max-height: 500px) {
#gv-legend-body {
    overflow-y: scroll;
    max-height: 200px;
}
}
#gv-legend-wrapper {
  display: block;
}



