/* ----------------------------------------------- */
/* BASE */
/* ----------------------------------------------- */

html, body {
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
}

/* ----------------------------------------------- */
/* Gestione colori customizzabili */
/* ----------------------------------------------- */

.leaflet-bar a, .leaflet-bar a:hover, .leaflet-bar a.leaflet-disabled {
  background-color: #24386c;
  color: #ccc;
}

.gv-color-scheme {
  background-color: #24386c;
  color: #CCC;
}

.gv-inverted-color-scheme {
  color: #24386c;
  background-color: #CCC;
}

.gv-secondary-color-scheme {
  background-color: #527c41;
  color: #CCC;
}

.gv-default-font {
  font-family: "Raleway",Arial,sans-serif !important;
  font-size: 14px;
  font-weight: bold;
}

/* ----------------------------------------------- */
/* LEAFLET */
/* ----------------------------------------------- */

/* LEAFLET MODIFICHE CUSTOM */

/* Impostazione cursore su mappa leaflet */
.leaflet-container {
  background: #fff;
  cursor: default;
}

/* Imposto z-index per bottoni in alto */
.leaflet-control-container {
  z-index: 800;
}

/* Toolbar */
.leaflet-bar {
  border-radius: 0px;
}

.leaflet-touch .leaflet-bar a {
  width: 26px;
  height: 26px;
  line-height: 26px;
}

.leaflet-touch .leaflet-control-layers, .leaflet-touch .leaflet-bar {
  border: 0px;
}

.leaflet-top.leaflet-left {
  left: 10px !important;
  top: 10px;
}

.leaflet-top.leaflet-left .leaflet-control {
  margin: 0px 0px;
}

.leaflet-bottom.leaflet-right {
  display: block;
  position: absolute;
  right: 10px !important;
  bottom: 10px;
}

.leaflet-bottom.leaflet-right .leaflet-control {
  margin: 0px 0px;
}

.leaflet-bar a:first-child {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

.leaflet-bar a:last-child {
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom: none;
}

.leaflet-control-attribution {
  margin-top: 10px !important;
  margin-left: 10px !important;
}

.leaflet-bar a, .leaflet-bar a:hover {
  border-bottom: none;
}

/* FINE LEAFLET MODIFICHE CUSTOM */

/* LEAFLET GEOCODER
TODO Levare dopo sviluppo modulo Geocoder Custom
*/
.leaflet-control-geocoder {
  background-color: #31708f;
  color: #ccc;
  font-size: 12px;
  display: block;
}

.leaflet-control-geocoder a, .leaflet-control-geocoder {
  border-bottom: none;
  display: inline-block;
}

.leaflet-control-geocoder .leaflet-control-geocoder-alternatives a {
  width: inherit;
  height: inherit;
  line-height: inherit;
}

.leaflet-control-geocoder a:hover, .leaflet-control-geocoder {
  border-bottom: none;
  display: inline-block;
}

.leaflet-control-geocoder-form {
  display: none;
  vertical-align: middle;
}

.leaflet-control-geocoder-expanded .leaflet-control-geocoder-form {
  display: inline-block;
}

.leaflet-control-geocoder-form input {
  font-size: 120%;
  border: 0;
  background-color: transparent;
  width: 246px;
}

.leaflet-control-geocoder-form-no-error {
  display: none;
}

.leaflet-control-geocoder-form input:focus {
  outline: none;
}

.leaflet-control-geocoder-form button {
  display: none;
}

.leaflet-control-geocoder-error {
  margin-top: 8px;
  margin-left: 8px;
  display: block;
  color: #444;
}

.leaflet-control-geocoder-alternatives {
  display: block;
  width: 272px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.leaflet-control-geocoder-alternatives-minimized {
  display: none;
  height: 0;
}

.leaflet-control-geocoder-alternatives li {
  white-space: nowrap;
  display: block;
  overflow: hidden;
  padding: 5px 8px;
  text-overflow: ellipsis;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}

.leaflet-control-geocoder-alternatives li a, .leaflet-control-geocoder-alternatives li a:hover {
  width: inherit;
  height: inherit;
  line-height: inherit;
  background: inherit;
  border-radius: inherit;
  text-align: left;
}

.leaflet-control-geocoder-alternatives li:last-child {
  border-bottom: none;
}

.leaflet-control-geocoder-alternatives li:hover, .leaflet-control-geocoder-selected {
  background-color: #f5f5f5;
}

.leaflet-control-geocoder-address-detail {

}

.leaflet-control-geocoder-address-context {
  color: #ccc;
}

/* CLUSTER MARKER */

.leaflet-cluster-anim .leaflet-marker-icon, .leaflet-cluster-anim .leaflet-marker-shadow {
  -webkit-transition: -webkit-transform 0.3s ease-out, opacity 0.3s ease-in;
  -moz-transition: -moz-transform 0.3s ease-out, opacity 0.3s ease-in;
  -o-transition: -o-transform 0.3s ease-out, opacity 0.3s ease-in;
  transition: transform 0.3s ease-out, opacity 0.3s ease-in;
}

.leaflet-cluster-spider-leg {
  /* stroke-dashoffset (duration and function) should match with leaflet-marker-icon transform in order to track it exactly */
  -webkit-transition: -webkit-stroke-dashoffset 0.3s ease-out, -webkit-stroke-opacity 0.3s ease-in;
  -moz-transition: -moz-stroke-dashoffset 0.3s ease-out, -moz-stroke-opacity 0.3s ease-in;
  -o-transition: -o-stroke-dashoffset 0.3s ease-out, -o-stroke-opacity 0.3s ease-in;
  transition: stroke-dashoffset 0.3s ease-out, stroke-opacity 0.3s ease-in;
}

.marker-cluster-small {
  background-color: rgba(181, 226, 140, 0.6);
}

.marker-cluster-small div {
  background-color: rgba(110, 204, 57, 0.6);
}

.marker-cluster-medium {
  background-color: rgba(241, 211, 87, 0.6);
}

.marker-cluster-medium div {
  background-color: rgba(240, 194, 12, 0.6);
}

.marker-cluster-large {
  background-color: rgba(253, 156, 115, 0.6);
}

.marker-cluster-large div {
  background-color: rgba(241, 128, 23, 0.6);
}

.marker-cluster {
  background-clip: padding-box;
  border-radius: 20px;
}

.marker-cluster div {
  width: 30px;
  height: 30px;
  margin-left: 5px;
  margin-top: 5px;

  text-align: center;
  border-radius: 15px;
  font: 12px "Helvetica Neue", Arial, Helvetica, sans-serif;
}

.marker-cluster span {
  line-height: 30px;
}

/* BOTTONI */

.easy-button {
  border: none;
  width: 26px;
  height: 26px;
  color: #ccc;
}

.leaflet-control-locate a {
  font-size: 1.4em;
  color: #ccc;
}

/* ----------------------------------------------- */
/* GV */
/* ----------------------------------------------- */

#gv-container {
  width: 100%;
  height: 100%;
}

#gv-title {
  position: absolute;
  left: 56px;
  top: 10px;
  height: 26px;
  z-index: 799;
  font-size: 16px;
  font-weight: bold;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  overflow-y: hidden;
  opacity: 0.6;
  filter: alpha(opacity=60);
  line-height: 28px;
  max-width: 800px;
}


.ap-input, .ap-hint {
  border-radius: 0px;
  border: 1px solid #24386c;
  color: #24386c;
  height: 26px;
  padding-left: 10px;
}

.ap-icon-pin {
  display: none;
}


/* ----------------------------------------------- */
/* ELEMENT */
/* ----------------------------------------------- */

.el-row {
  padding: 5px 5px 5px 15px;
}

.row-bg {
  background-color: #CCC;
}

.el-button--primary {
  color: #CCC;
  background-color: #24386C !important;
  border-color: #24386C !important;
}

.el-button.is-disabled, .el-button.is-disabled:focus, .el-button.is-disabled:hover {
  color: #bfcbd9;
  cursor: default !important;
  background-image: none;
}

.el-button--inverted {
  color: #24386C !important;
  background-color: #ccc !important;
  border-color: #ccc !important;
}

.el-select-dropdown__wrap {
  overflow: auto;
}

.el-button--mini, .el-button--mini.is-round {
  padding: 4px 10px;
}

.el-select-dropdown__list {
  max-width: 400px;
}

.el-select-dropdown__item {
  height: 40px;
  font-size: 12px;
}



